.header {
    &__top, &__bottom {
        &-inner {
            align-items: center;
            justify-content: space-between;
        }
    }
    &__top {
        justify-content: space-between;
        border-bottom: 1px solid var(--borderColor);
        padding: 17px 35px;
        &-inner {
            & > div {
                align-items: center;
                justify-content: flex-end;
            }
            .dropdown, & > .flexRow {
                width: 70.3px;
            }
        }
    }
    &__bottom {
        padding: 21px 35px 19px;
        border-bottom: 1px solid var(--borderColor);
        li {
            margin-right: 50px;
        }
        &-menu {
            display: none;
        }
    }
    &__logo {
        flex-shrink: 0;
    }
}
@media(max-width: 900px) {
    .header {
        &__top, &__bottom {
            padding-left: 0;
            padding-right: 0;
        }
        &__bottom {
            position: relative;
            &-menu {
                display: flex;
                font-size: 18px;
            }
            nav {
                display: none;
                position: absolute;
                top: calc(100% - 5px);
                left: 15px;
                background-color: var(--White);
                padding: 15px;
                ul {
                    display: block;
                }
                li {
                    margin: 0 0 15px;
                    &:last-child {
                        margin: 0;
                    }
                }
            }
        }
    }
}
@media(max-width: 450px) {
    .header {
        &__top, &__bottom {
            padding: 12px 0;
        }
        &__logo {
            margin-left: -15px;
            img {
                width: 160px;
            }
        }
    }
}